import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';

let DetailBox = styled.div`
  padding : 20px;
`;

let DetailSubject = styled.h4`
  font-size : 25px;
  color : ${ props => props.color }
`;

function Detail(props) {
  let history = useHistory();
  let { id } = useParams();
  let product = props.shoes.find(function (a) {
    return a.id == id
  });

  return (
    <div className="container">
      <DetailBox>
        <DetailSubject color="red">상세페이지</DetailSubject>
      </DetailBox>
      <div className="row">
        <div className="col-md-6">
          <img src={"https://codingapple1.github.io/shop/shoes" + (product.id + 1) + ".jpg"} width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{product.title}</h4>
          <p>{product.content}</p>
          <p>{product.price}</p>
          <button className="btn btn-danger">주문하기</button>
          <button className="btn btn-danger" onClick={() => { history.goBack(); }}>뒤로가기</button>
        </div>
      </div>
    </div>
  );
}

export default Detail;
